<template>
  <header class="fixed-top p-3" :class="{'top-hide': direction}">
    <router-link to="">
      HUNG JI
    </router-link>
  </header>
  <section ref="banner" class="banner_page position-relative mb-5">
    <div
      class="top_img parallax"
      data-depth="0.40"
      :style="{'background-image': `url(${require('@/assets/img/bg.jpg')})`}"
      ref="parallax"
    ></div>
    <div class="container h-100 position-relative z-index-1">
      <div class="row h-100 mx-0 justify-content-center align-items-center overflow-hidden">
        <div class="col-auto text-center box">
          <h2 class="page-title">作品集</h2>
          <span>works</span>
        </div>
      </div>
    </div>
  </section>
  <div class="container">
    <router-view :banner="banner"/>
  </div>
  <div class="goTop btn btn-primary" :class="positionY >= 500 ? 'goTopAfter' : '' " @click="goTop">TOP</div>
</template>

<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)
export default {
  data () {
    return {
      direction: false,
      positionY: 0,
      banner: null
    }
  },
  methods: {
    handleScroll () {
      const scrollY = window.scrollY
      if (scrollY > this.positionY) {
        this.direction = true
      } else {
        this.direction = false
      }
      this.positionY = scrollY
    },
    simpleScrollTrigger () {
      const depth = this.$refs.parallax.dataset.depth
      const movement = -(this.$refs.parallax.offsetHeight * depth)
      gsap.to('.parallax', {
        y: movement,
        ease: 'none',
        scrollTrigger: {
          trigger: 'body',
          start: 'top',
          scrub: 1
        }
      })
      gsap.from('.box', {
        y: 50,
        opacity: 0,
        duration: 1.5,
        scrollTrigger: {
          trigger: '.box',
          start: 'top 90%'
        }
      })
    },
    goTop () {
      document.documentElement.scrollTop = 0
    }
  },
  mounted () {
    this.simpleScrollTrigger()
    window.addEventListener('scroll', this.handleScroll)
    this.banner = this.$refs.banner.clientHeight
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>
<style lang="scss">
@import "./assets/all.scss";
.goTop {
  visibility: hidden;
  transform: translateY(80px);
  position: fixed;
  bottom: 15px;
  right: 15px;
  z-index: 2;
  transition: all 1.2s cubic-bezier(.19,1,.22,1);
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  width: 53px;
  height: 53px;
  border-radius: 100%;
  padding-top: 5px;
  font-size: 14px;
  &::before{
    content: '';
    background-image: url(./assets/img/up.svg);
    background-position: center;
    background-repeat: no-repeat;
    width: 30px;
    height: 30px;
  }
  &.goTopAfter {
    visibility: visible;
    transform: translateY(0);
  }
  &:hover{
    color: #fff;
  }
}
</style>
